import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import HackerPage from './pages/HackerPage';
import VillejuifPage from './pages/VillejuifPage';
import NoisyPage from './pages/NoisyPage';
import PalaiseauPage from './pages/PalaiseauPage';
import OccupantDashboard from './components/occupants/OccupantDashboard';

function App() {
  return (
    // Base = emplacement rel du build sur le site
    <Router basename="/analyse/ECLA">
      <Routes>
        {/* Page daccueil de lapp ECLA */}
        <Route path="/" element={<OccupantDashboard residence="ecla" saison="hiver" />} />

        {/* (Optionnel) autres pages si vous les voulez accessibles sous /analyse/ECLA/... */}
        <Route path="/hacker" element={<HackerPage />} />
        <Route path="/villejuif" element={<VillejuifPage />} />
        <Route path="/noisy-le-grand" element={<NoisyPage />} />
        <Route path="/palaiseau_1" element={<PalaiseauPage />} />

        {/* Catch-all vers la racine */}
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </Router>
  );
}

export default App;
